import { Box, Button, Checkbox, FormLabel, TextField, Typography } from '@mui/material'
import React, { useState } from 'react'
import { addMovie } from '../../api-helpers/apiHelpers';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';



const labelProps = {
    mt: 1,
    mb: 1,
    
}

const AddMovie = () => {

    const [inputs,setInputs] = useState({title:"",description:"",posterUrl:"",releaseDate:"",featured:false,});
    const [actors,setActors] = useState([]);
    const [actor,setActor] = useState("");

    const handleChange = (e)=>{
        setInputs((prevState)=>({
            ...prevState,
            [e.target.name] : e.target.value
        }))
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        console.log(inputs,actors);
        addMovie({...inputs,actor:actors}).then(res=>console.log(res)).catch((err)=>console.log(err));
        toast.success('Movie Added Successfully');   
    }

    return (
        <div>
            <form onSubmit={handleSubmit} >
                <Box width={'50%'} padding={10} margin={"auto"} display={'flex'} flexDirection={'column'} boxShadow={"10px 10px 20px #ccc"} >

                    <Typography textAlign={'center'} variant='h5' fontFamily={"verdana"}>Add New Movie</Typography>
                    <FormLabel sx={labelProps}>Title</FormLabel>
                    <TextField value={inputs.title} onChange={handleChange} name='title' variant='standard' margin='normal' />
                    <FormLabel sx={labelProps}>Description</FormLabel>
                    <TextField value={inputs.description} onChange={handleChange}  name='description' variant='standard' margin='normal' />
                    <FormLabel sx={labelProps}>Poster Url</FormLabel>
                    <TextField value={inputs.posterUrl} onChange={handleChange}  name='posterUrl' variant='standard' margin='normal' />
                    <FormLabel sx={labelProps}>Release Date</FormLabel>
                    <TextField value={inputs.releaseDate} onChange={handleChange} type='date'  name='releaseDate' variant='standard' margin='normal' />

                    <FormLabel sx={labelProps}>Actors</FormLabel>
                    <Box display={'flex'} >
                        <TextField value={actor} name='actor' onChange={(e)=>setActor(e.target.value)} variant='standard' margin='normal' />
                        <Button onClick={()=>{setActors([...actors,actor])}} >Add</Button>
                    </Box>

                    {/* <FormLabel  sx={labelProps}>Feature</FormLabel> */}
                    {/* <Checkbox name="featured" checked={inputs.featured}  onClick={(e)=>setInputs((prevState)=>({...prevState,featured : e.target.checked}))} sx={{mr:"auto"}} /> */}
                    <Button type='submit' variant='contained' sx={{width:"30%",bgcolor:"#2b2d42",margin:"auto",":hover":{bgcolor:"#121217"}}}>Add New Movie</Button>
                </Box>
            </form>
            <ToastContainer /> 
        </div>
    )
}

export default AddMovie